.slider {
  padding-top: 112px;
  border-top: 1px solid var(--color-gray-2);
  @media (max-width:768px) {
    padding-top: 56px;
  }
  @media (max-width:425px) {
    border-top: none;
    padding-top: 64px;
  }
  .slider-title-container {
    @media (max-width:768px) {
      position: absolute;
      flex-direction: column;
      z-index: 1;
      align-items: normal;
    }
  }
  .slider-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 56px;
    color: var(--color-dark-2);
    text-transform: uppercase;
    .slider-color {
      color: var(--color-gold-1);
      margin-right: 10px;
    }
    @media (max-width:768px) {
      font-size: 36px;
      .slider-color {
        margin-right: 5px;
      }
    }
    @media (max-width:768px) {
      font-size: 28px;
      .slider-color {
        margin-right: 4px;
      }
    }
  }
  .slider-title-button {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-dark-2);
    transition: 300ms;
    @media (max-width:768px) {
      position: static;
      z-index: 4;
      margin-top: 510px;
    }
    @media (max-width:425px) {
      margin-top: 500px;
    }
    .button-icon {
      width: 16px;
      height: 16px;
      border: 1.5px solid var(--color-dark-2);
      border-radius: 100%;
      margin-left: 8px;
      transition: 300ms;
      svg {
        transition: 300ms;
        fill: none;
        stroke: var(--color-dark-2);
      }
    }
    &:hover {
      color: var(--color-gold-1);
      .button-icon {
        border: 1.5px solid var(--color-gold-1);
        svg {
          stroke: var(--color-gold-1);
        }
      }
    }
  }
  .cards-container {
    margin-top: 48px;
    @media (max-width:768px) {
      margin-top: 80px;
    }
    @media (max-width:425px) {
      margin-top: 88px;
    }
    .card {
      cursor: pointer;
      position: relative;
      background-color: var(--color-gray-1);
      border-radius: 8px;
      border: 1px solid transparent;
      padding-bottom: 24px;
      transition: 300ms;
      user-select: none;
      max-width: 256px;
      .tags {
        padding: 0 24px;
        position: absolute;
        width: 100%;
        top: 28px;
        .message {
          padding: 5px 12px;
          background-color: var(--color-red-1);
          font-size: 14px;
          line-height: 18px;
          color: var(--color-light-1);
          border-radius: 100px;
          white-space: nowrap;
        }
        .favorite {
          fill: var(--color-gray-3);
          transition: 300ms;
          &:hover {
            fill: var(--color-dark-2);
            opacity: 0.4;
          }
          &:active {
            transition: 100ms;
            opacity: 1;
            fill: var(--color-gold-1);
          }
        }
      }
      .img-container {
        user-select: none;
      }
      .txt-container {
        padding: 0 24px;
        padding-top: 24px;
        padding-bottom: 16px;
        .txt-title {
          font-family: 'PF Regal Display Pro', Arial, Helvetica, sans-serif;
          font-style: italic;
          font-size: 18px;
          line-height: 18px;
          color: var(--color-dark-2);
          padding-bottom: 8px;
          transition: 300ms;
        }
        .txt-about {
          font-size: 14px;
          line-height: 18px;
          color: var(--color-dark-2);
          opacity: 0.8;
          padding-bottom: 8px;
        }
      }
      .price-container {
        padding: 0 24px;
        padding-top: 8px;
        .new-price {
          font-family: 'PF Regal Display Pro', Arial, Helvetica, sans-serif;
          font-style: italic;
          font-size: 18px;
          line-height: 18px;
          color: var(--color-dark-2);
        }
        .old-price {
          font-family: 'PF Regal Display Pro', Arial, Helvetica, sans-serif;
          font-style: italic;
          font-size: 18px;
          line-height: 18px;
          text-decoration-line: line-through;
          color: var(--color-dark-2);
          opacity: 0.4;
          margin-left: 16px;
        }
      }
      &:hover {
        border: 1px solid rgba(33, 36, 46, 0.4);
        .txt-title {
          color: var(--color-gold-1);
        }
      }
    }
  }
}