.modal {
  display: none;
  &.open {
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background-color: var(--color-light-1);
    @media (max-width:768px) {
      overflow-y: scroll;
    }
  }
  .w-container {
    height: 100%;
    .modal-container {
      padding-top: 24px;
      padding-bottom: 268px;
      @media (max-width:1280px) {
        padding-bottom: 150px;
      }
      @media (max-width:768px) {
        padding-bottom: 60px;
      }
      @media (max-width:425px) {
        padding-bottom: 0;
      }
      .cross {
        cursor: pointer;
        svg {
          fill: var(--color-dark-2);
          transition: 300ms;
        }
        &:hover {
          svg {
            fill: var(--color-gold-1);
          }
        }
        &:active {
          opacity: 0.4;
        }
      }
      .logo {
        cursor: pointer;
        transition: 300ms;
        &:hover {
          opacity: 0.72;
        }
      }
    }
    .navigation {
      height: 100%;
      @media (max-width:768px) {
        flex-direction: column;
       }
       @media (max-width:425px) {
         padding-top: 56px;
       }
       .navigation-container {
        @media (max-width:768px) {
          margin-bottom: 24px;
          &:last-child {
            margin-bottom: 0;
          }
        }
        @media (max-width:425px) {
          margin-bottom: 32px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      .nav-title {
        font-weight: 500;
        font-size: 24px;
        line-height: 24px;
        color: var(--color-dark-2);
        padding-bottom: 24px;
        transition: 300ms;
        &:hover {
          color: var(--color-gold-1);
        }
        @media (max-width:425px) {
          padding-bottom: 16px;
        }
      }
      .nav-link {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: var(--color-dark-2);
        padding-bottom: 16px;
        transition: 300ms;
        &:hover {
          opacity: 0.8;
        }
        @media (max-width:425px) {
          padding-bottom: 8px;
        }
      }
      .contact-link {
        font-size: 16px;
        line-height: 24px;
        color: var(--color-dark-2);
        padding-bottom: 16px;
        transition: 300ms;
        svg {
          transition: 300ms;
          fill: var(--color-dark-2);
          margin-right: 8px;
        }
        .phone {
          svg {
            transition: 300ms;
            fill: none;
            stroke: var(--color-dark-2);
          }
        }
        .geo {
          margin-top: 5px;
        }
        &:hover {
          color: var(--color-gold-1);
          svg {
            fill: var(--color-gold-1);
          }
          .phone {
            svg {
              fill: none;
              stroke: var(--color-gold-1);
            }
          }
        }
      }
    }
  }
}