.footer {
  margin-top: 112px;
  background-color: #161612;
  .navigation {
    padding-top: 56px;
    @media (max-width:768px) {
     flex-direction: column;
    }
    @media (max-width:425px) {
      padding-top: 64px;
    }
    .navigation-container {
      @media (max-width:768px) {
        margin-bottom: 24px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      @media (max-width:425px) {
        margin-bottom: 32px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .nav-title {
      font-weight: 500;
      font-size: 24px;
      line-height: 24px;
      color: var(--color-light-1);
      padding-bottom: 24px;
      transition: 300ms;
      &:hover {
        color: var(--color-gold-1);
      }
      @media (max-width:425px) {
        padding-bottom: 16px;
      }
    }
    .nav-link {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--color-light-1);
      padding-bottom: 16px;
      transition: 300ms;
      &:hover {
        opacity: 0.8;
      }
      @media (max-width:425px) {
        padding-bottom: 8px;
      }
    }
    .contact-link {
      font-size: 16px;
      line-height: 24px;
      color: var(--color-light-1);
      padding-bottom: 16px;
      transition: 300ms;
      svg {
        transition: 300ms;
        fill: var(--color-light-1);
        margin-right: 8px;
      }
      .phone {
        svg {
          transition: 300ms;
          fill: none;
          stroke: var(--color-light-1);
        }
      }
      .geo {
        margin-top: 5px;
      }
      &:hover {
        color: var(--color-gold-1);
        svg {
          fill: var(--color-gold-1);
        }
        .phone {
          svg {
            fill: none;
            stroke: var(--color-gold-1);
          }
        }
      }
    }
  }
  .footer-block {
    padding: 32px 0;
    margin-top: 50px;
    border-top: 1px solid rgba(255, 255, 255, 0.16);
    border-bottom: 1px solid rgba(255, 255, 255, 0.16);
    @media (max-width:425px) {
      margin-top: 24px;
      padding: 40px 0;
    }
    .footer-wrapper {
      @media (max-width:425px) {
        flex-direction: column;
        align-items: normal;
        justify-content: normal;
      }
    }
    .social-container {
      @media (max-width:425px) {
        margin-bottom: 16px;
      }
    }
    .social-circle {
      width: 32px;
      height: 32px;
      background-color: rbga(255, 255, 255, 0.08);
      border: 1px solid rgba(255, 255, 255, 0.16);
      border-radius: 100%;
      transition: 300ms;
      &:not(:last-child) {
        margin-right: 8px;
      }
      &:hover {
        border: 1px solid var(--color-gold-1);
      }
    }
    .security {
      font-size: 14px;
      line-height: 16px;
      color: var(--color-light-1);
      opacity: 0.4;
      transition: 300ms;
      &:hover {
        opacity: 0.72;
      }
      @media (max-width:425px) {
        margin-bottom: 16px;
      }
    }
    .company {
      font-size: 14px;
      line-height: 18px;
      color: var(--color-light-1);
      opacity: 0.4;
      transition: 300ms;
      svg {
        margin-left: 8px;
      }
      &:hover {
        opacity: 0.72;
      }
    }
  }
  .footer-offer {
    padding: 32px 38px;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    color: var(--color-light-1);
    opacity: 0.4;
    @media (max-width:425px) {
      padding-top: 40px;
      padding-bottom: 64px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }
}