.profile {
  position: relative;
  width: 100%;
  .breadcrumbs {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    height: 100%;
    padding-top: 112px;
    padding-left: 40px;
    padding-right: 80px;
    padding-bottom: 112px;
    border-right: 1px solid var(--color-gray-2);
    @media (max-width:1280px) {
      position: absolute;
      top: 0;
      left: 0;
      height: auto;
      width: 100%;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 36px 40px;
      border-right: none;
      border-bottom: 1px solid var(--color-gray-2);
    }
    @media (max-width:768px) {
      padding: 24px 40px;
    } 
    @media (max-width:425px) {
      padding: 8px 16px;
    }
    .bc-link {
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      white-space: nowrap;
      color: var(--color-dark-2);
      margin-top: 12px;
      transition: 300ms;
      &:first-child {
        color: var(--color-gold-1);
        margin-top: 0;
      }
      &:hover {
        color: var(--color-gold-1);
      }
      @media (max-width:1280px) {
        margin-top: 0;
      }
    }
  }
  .profile-title {
    width: 1062px;
    position: relative;
    padding-top: 112px;
    padding-bottom: 80px;
    @media (max-width:1280px) {
      width: 100%;
      padding-top: 156px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding-bottom: 0;
    }
    @media (max-width:768px) {
      align-items: normal;
      justify-content: normal;
      padding-top: 120px;
      padding-bottom: 10px;
    }
    @media (max-width:425px) {
      padding-top: 73px;
      padding-bottom: 0;
    }
    h1 {
      font-weight: 400;
      font-size: 80px;
      line-height: 80px;
      color: var(--color-dark-2);
      text-transform: uppercase;
      @media (max-width:1280px) {
        font-size: 68px;
        line-height: 68px;
      }
      @media (max-width:768px) {
        font-size: 56px;
        line-height: 56px;
      }
      @media (max-width:425px) {
        font-size: 35px;
        line-height: 40px;
      }
    }
    &::before {
      position: absolute;
      bottom: 0;
      left: 186px;
      width: calc(50vw + 640px - 233px);
      height: 1px;
      background: var(--color-gray-2);
      content: '';
      @media (max-width:1280px) {
        display: none;
      }
    }
  }
  .profile-form {
    position: relative;
    @media (max-width:1280px) {
      display: flex;
      justify-content: center;
    }
    @media (max-width:768px) {
      justify-content: normal;
      width: 100%;
    }
    &::before {
      position: absolute;
      bottom: 0;
      left: 186px;
      width: calc(50vw + 640px - 233px);
      height: 1px;
      background: var(--color-gray-2);
      content: '';
      @media (max-width:1280px) {
        display: none;
      }
      @media (max-width:425px) {
        display: block;
        left: -100px;
        width: calc(50vw + 640px - 378px);
      }
      @media (max-width:320px) {
        width: calc(50vw + 640px - 400px);
      }
    }
    .profile-wrapper {
      @media (max-width:768px) {
        width: 100%;
      }
    }
    h3 {
      width: 544px;
      font-weight: 500;
      font-size: 24px;
      line-height: 24px;
      color: var(--color-dark-2);
      margin-top: 80px;
      margin-bottom: 48px;
      @media (max-width:1280px) {
        display: flex;
        justify-content: center;
      }
      @media (max-width:768px) {
        width: 100%;
        justify-content: normal;
        margin-top: 56px;
      }
      @media (max-width:425px) {
        margin-top: 32px;
        font-size: 20px;
        line-height: 20px;
        margin-bottom: 32px;
      }
    }
    form {
      width: 544px;
      padding-bottom: 80px;
      @media (max-width:1280px) {
        padding-bottom: 0;
      }
      @media (max-width:768px) {
        width: 100%;
      }
      @media (max-width:425px) {
        padding-bottom: 16px;
      }
      label {
        position: relative;
        font-size: 16px;
        line-height: 24px;
        color: rgba(33, 36, 46, 0.4);
        margin-bottom: 24px;
        @media (max-width:768px) {
          width: 100%;
        }
        @media (max-width:425px) {
          margin-bottom: 16px;
        }
      }
      input {
        max-width: 544px;
        height: 56px;
        padding: 16px;
        border: none;
        background-color: var(--color-gray-1);
        font-size: 16px;
        line-height: 24px;
        color: var(--color-dark-2);
        margin-top: 12px;
        border-radius: 4px;
        outline: none;
        &:last-child {
          margin-bottom: 0;
        }
        &::placeholder {
          color: var(--color-dark-2);
          opacity: 0.4;
        }
        @media (max-width:768px) {
          max-width: 100%;
        }
        @media (max-width:425px) {
          margin-top: 8px;
        }
      }
      #old-password {
        color: var(--color-dark-2);
        opacity: 0.4;
      }
      .password-control {
        svg {
          position: absolute;
          z-index: 2;
          stroke: #A1A3A7;
          cursor: pointer;
          top: 55%;
          right: 5%;
          transition: 300ms;
          &:hover {
            stroke: var(--color-dark-2);
          }
        }
      }
    }
  }
  .button-container {
    width: 898px;
    padding-bottom: 112px;
    @media (max-width:1280px) {
      width: 100%;
      display: flex;
      align-items: center;
      padding-bottom: 80px;
    }
    @media (max-width:768px) {
      padding-bottom: 56px;
    }
    @media (max-width:425px) {
      padding-bottom: 0;
    }
    .profile-button {
      width: 230px;
      height: 56px;
      white-space: nowrap;
      margin-top: 80px;
      background-color: var(--color-dark-2);
      border-radius: 4px;
      text-align: center;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: var(--color-light-1);
      transition: 300ms;
      &:hover {
        background-color: var(--color-gold-1);
      }
      @media (max-width:768px) {
        margin-top: 56px;
        width: 100%;
      }
      @media (max-width:425px) {
        margin-top: 32px;
      }
    }
  }
}