.w-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 38px;
  @media (max-width:425px) {
    padding: 0 16px;
  }
}

.flex {
  display: flex;
  &.column {
    flex-direction: column;
  }
  &.j-between {
    justify-content: space-between;
  }
  &.j-center {
    justify-content: center;
  }
  &.a-center {
    align-items: center;
  }
  &.a-end {
    align-items: flex-end;
  }
}
.border-1 {
  border-bottom: 12px solid var(--color-brown-1);
}
.border-2 {
  border-bottom: 12px solid var(--color-brown-2);
}
.border-3 {
  border-bottom: 12px solid var(--color-brown-3);
}
