.header {
  padding: 20px 0;
  border-bottom: 1px solid var(--color-gray-2);
  .logo {
    cursor: pointer;
    transition: 300ms;
    &:hover {
      opacity: 0.72;
    }
  }
  .header-link {
    padding-right: 20px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--color-dark-2);
    stroke: var(--color-dark-2);
    transition: 300ms;
    &:last-child {
      padding-right: 0;
    }
    svg {
      transition: 300ms;
      margin-left: 5px;
    }
    &:hover {
      stroke: var(--color-gold-1);
      color: var(--color-gold-1);
    }
    @media (max-width:1140px) {
      display: none;
    } 
  }
  .phone-header {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--color-gold-1);
    stroke: var(--color-gold-1);
    transition: 300ms;
    svg {
      margin-right: 10px;
    }
    &:hover {
      color: var(--color-dark-2);
      stroke: var(--color-dark-2);
    }
    @media (max-width:768px) {
      display: none;
    } 
  }
  .icon-container {
    svg {
      cursor: pointer;
      stroke: var(--color-gold-1);
      margin-right: 18px;
      transition: 300ms;
      &:last-child {
        stroke: none;
        fill: var(--color-gold-1);
        margin-right: 0;
      }
      &:hover {
        transition: 300ms;
        stroke: var(--color-dark-2);
        &:last-child {
          stroke: none;
          fill: var(--color-dark-2);
        }
      }
      @media (max-width:425px) {
        margin-right: 8px;
      }
    }
  }
  .burger {
    cursor: pointer;
    .burger-static {
      svg {
        fill: var(--color-dark-1);
      }
    }
    .burger-hover {
      display: none;
    }
    &:hover {
      .burger-static {
        display: none;
      }
      .burger-hover {
        display: block;
      }
    }
  }
}